.card {
    position: relative;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* Front */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front {
    transform: rotateY(0deg);
}

/* Back */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
    filter: brightness(85%) 
}

.flipped .back {
    transform: rotateY(90deg);
    transition: 0s;
}



